export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			date: currentDate,
			isShow: true,
			isChecked: "",
			userinfoData: {
				userName: '',
				gender: '',
				phone: '',
				openId: '',
				birthday: ''
			},
		}
	},
	onLoad(option) {
		let title = '修改个人信息';
		this.userinfoData = JSON.parse(decodeURIComponent(option.query))
		this.isChecked = this.userinfoData.gender
		this.userinfoData.nickName = this.userinfoData.userName
		uni.setNavigationBarTitle({
			title
		})
		if (this.global.hasPhone(this)) {
			this.isShow = false;
			this.userinfoData.phone = this.$cache.get(this.global.USER_PHONE);
		}
		if (this.userinfoData.birthday != null && this.userinfoData.birthday != '') {
			this.isChecked = this.userinfoData.gender
			this.date = this.userinfoData.birthday.slice(0, 4) + "-" + this.userinfoData.birthday.slice(4, 6) +
				"-" + this.userinfoData.birthday.slice(6, 8);
		}
	},

	onShow() {
		if (this.userinfoData.birthday != null && this.userinfoData.birthday != '') {
			this.isChecked = this.userinfoData.gender
			this.date = this.userinfoData.birthday.slice(0, 4) + "-" + this.userinfoData.birthday.slice(4, 6) +
				"-" + this.userinfoData.birthday.slice(6, 8);
		}
	},
	methods: {
		switchChange(e) {
			this.addressData.default = e.detail;
		},

		bindDateChange: function(e) {
			this.date = e.target.value
		},

		radioChange: function(e) {
			console.log(e)
			this.isChecked = e;
			this.userinfoData.gender = e;
		},


		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},

		//提交
		confirm() {
			let data = this.userinfoData;
			console.log("11", this.userinfoData)
			if (!data.userName) {
				this.$msg('请输入用户名');
				return;
			}
			if (!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.phone)) {
				this.$msg('请输入正确的手机号码');
				return;
			}
			data.birthday = this.date
			//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
			// this.$api.prePage().refreshList(data, this.manageType);
			// this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
			this.$minApi.uniapp_post(this.$base_api.updatePersonInfo, JSON.stringify(data), 'json').then(res => {
				let userInfo = this.$cache.get(this.global.USER_INFO);
				userInfo.nickName = data.userName;
				userInfo.gender = data.gender;
				this.$cache.set(this.global.USER_INFO, userInfo);
				this.$cache.set(this.global.USER_PHONE, data.phone);
			})
			setTimeout(() => {
				uni.navigateBack()
			}, 800)
		},
	}
}
